Random.extend({
  tag: function () {
    const tag = ['', '', '', '']
    return this.pick(tag)
  }
})

interface ITableList {
  list: Array<{
    date: string
    name: string
    address: string
    tag: '' | '' | '' | ''
    amt: number
  }>
}

const tableList: ITableList = mock({
  //  list  1  10 
  'list|100': [{
    //  id  1 1
    'id|+1': 1,
    date: () => Random.date('yyyy-MM-dd'),
    name: () => Random.name(),
    address: () => Random.cparagraph(1),
    tag: () => Random.tag(),
    amt: () => Number(Random.float(-100000, 100000).toFixed(2))
  }]
})
mock('/getTableList', 'get', (req: IReq) => {
  const {page, size, tag} = JSON.parse(req.body)
  const data = tag === '' ? tableList.list : tableList.list.filter(v => v.tag === tag)
  return mock({
    code: 200,
    data: {
      data: data.filter((v, i) => i >= (page - 1) * size && i < page * size),
      total: data.length
    },
    message: ''
  })
})
